<template>
  <!-- 02, 03-->
  <div>
    <SectionTitle :title="sectionTitle" />
    <Tabs :titles="titles" :tabLen="tabLen">
      <template v-slot:1>
        <Accordion id="acc1_1" title="問題保單">
          <Message title="問題描述" :content="msgContent" />
          <TestTabulator
            :tableId="slot1Table1Id"
            :columns="slot1Table1Columns"
            :tableData="slot1Table1Data"
            checkId1="home-slot1table1-check1"
            checkId2="home-slot1table1-check2"
          />
        </Accordion>
        <Accordion id="acc1_2" title="保戶訊息">
          <TestTabulator
            :tableId="slot1Table2Id"
            :columns="slot1Table2Columns"
            :tableData="slot1Table2Data"
            checkId1="home-slot1table2-check1"
            checkId2="home-slot1table2-check2"
          />
        </Accordion>
      </template>
      <template v-slot:2>
        <Accordion id="acc2_1" title="要保人資料">
          <Tags :tags="slot2Tags" />
          <TestTabulator
            :tableId="slot2Table1Id"
            :columns="slot2Table1Columns"
            :tableData="slot2Table1Data"
            checkId1="home-slot2table1-check1"
            checkId2="home-slot2table1-check2"
          />
        </Accordion>
        <Accordion id="acc2_2" title="要保人特定資料">
            <Table :colNum="2" :table="slot2Table2" />
        </Accordion>
      </template>
    </Tabs>
  </div>
</template>

<script>
import { v4 as uuid } from "uuid";
export default {
  name: "insPolicyDetails",
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.$store.commit("setHistory", {
        name: from.name,
        path: from.path,
        id: uuid(),
      });
    });
  },
  computed: {
    table1Data() {
      let data = this.$store.state.singleInsPolicyData;
      console.log(data);
      let id = data ? data.id : "no id";
      let originalTable1Data = this.table1;
      if (data) {
        for (let i = 0; i < originalTable1Data.length; i++) {
          originalTable1Data[i]["value"] = `${id}_newData`;
        }
      }
      return originalTable1Data;
    },
  },
  data() {
    return {
      sectionTitle: "提示訊息",
      tabLen: 2,
      titles: ["訊息", "要保人資料"],
      msgContent: "問題描述_設計文字",
      slot1Table1Id: "homeSlot1Table1",
      slot1Table2Id: "homeSlot1Table2",
      slot2Table1Id: "homeSlot2Table1",
      slot1Table1Columns: [
        { title: "種類", field: "a" },
        { title: "處理者", field: "b" },
        { title: "處理日期", field: "c" },
        { title: "問題描述", field: "d" },
      ],
      slot1Table2Columns: [
        { title: "保單號碼", field: "a" },
        { title: "訊息種類", field: "b" },
        { title: "訊息內容", field: "c" },
        { title: "通知日期", field: "d" },
        { title: "發送單位", field: "e" },
      ],
      slot2Table1Columns: [
        { title: "保單號碼", field: "a" },
        { title: "收費地址指示", field: "b" },
        { title: "住所地址指示", field: "c" },
        { title: "無效收費地址", field: "d" },
        { title: "收費地址", field: "e" },
        { title: "住所地址", field: "f" },
      ],
      slot1Table1Data: [],
      slot1Table2Data: [],
      slot2Table1Data: [],
      slot2Table2: [
        { key: "列管/手機", keyId: 0, value: "列管/手機_設計文字" },
        { key: "電子表單/驗證", keyId: 1, value: "電子表單/驗證_設計文字" },
        { key: "列管/E-mail", keyId: 2, value: "列管/E-mail_設計文字" },
        { key: "自動化交易申請", keyId: 3, value: "自動化交易申請_設計文字" },
        { key: "失聯保戶", keyId: 4, value: "失聯保戶_設計文字" },
        { key: "投資風險屬性", keyId: 5, value: "投資風險屬性_設計文字" },
        { key: "FATCA指示", keyId: 6, value: "FATCA指示_設計文字" },
        { key: "未領還本金", keyId: 7, value: "未領還本金_設計文字" },
        { key: "簽樣卡", keyId: 8, value: "簽樣卡_設計文字" },
      ],
      slot2Tags: [{ title: "要保人姓名/ID", detail: "要保人姓名/ID_設計文字" }],
    };
  },
};
</script>
